body{
    margin: 0;
}
.index{
    width: 390px; height: 844px;
    background-color: lightblue;
    margin: 0 auto;
    text-align: center;
    overflow: hidden;
    h2{
        margin-top: 150px ;
    }
    .index_btn{
        width: 100px; height: 100px;
        background-color: azure;
        border-radius: 50%;
        line-height: 100px;
        font-size: 35px;
        margin: 100px auto 20px;
        &:hover{
            background-color: pink;
        }
    }
}
.menu_box{
    width: 90%; padding: 25px 0 80px;
    background-color: white;
    margin: 0 auto;
    border-radius: 20px;
    .menu{
        font-size: 25px;
    }
    
}